import React from 'react';
import CardMultiCheckboxes from './CardMultiCheckboxes';
import { useIntl } from "react-intl"

const UtilitiesInformation = ({ data }) => {
    const { formatMessage: f } = useIntl()

    const informationUtilitiesLefts = data.utilityData ? data.utilityData.filter(obj => obj.realEstateUtilitiesId % 2) : []
    const informationUtilitiesRights = data.utilityData ? data.utilityData.filter(obj => obj.realEstateUtilitiesId % 2 === 0) : []

    const leftData = [
        {
            text: "Nhà xe"
        },
        {
            text: "Sân thượng"
        },
    ]

    const rightData = [
        {
            text: "Sân vườn"
        },
        {
            text: "Solar"
        },
    ]

    return <CardMultiCheckboxes title={f({ id: "utilityData" })} leftData={leftData} rightData={rightData} />
}

export default UtilitiesInformation