import React, { memo } from 'react';

import Avatar from "@src/assets/images/avatars/1.png";

import { ReactComponent as ButtonCall } from "@src/assets/images/icons/button-icons/call.svg";
import { ReactComponent as IconRibbon } from "@src/assets/images/icons/ribbon.svg";

import * as styles from '../styles/ContactInfo.module.scss'

import { injectIntl } from "react-intl";

const ContactInfo = (props) => {
  const { intl } = props;
  const defaultFields = [
    {
      label: intl.formatMessage({ id: "name" }),
      value: "Mrs. Woman 1"
    },
    {
      label: intl.formatMessage({ id: "birthYear" }),
      value: "1969"
    },
    {
      label: intl.formatMessage({ id: "phone" }),
      value: "+840999887676"
    },
    {
      label: intl.formatMessage({ id: "email" }),
      value: "chudautu@gmail.com"
    },
  ];

  return (
    <div className={styles.contactInfo}>
      {[1, 2].map((_, index) => (
        <div key={index}>
          <div className={styles.headerContact}>
            <div className={styles.headerContactLeft}>
              <div className={styles.profileImgContainer}>
                <img className={styles.profileImg} src={Avatar} alt="" />
              </div>
              <div className={styles.profileTitles}>
                <div className={styles.profileTitle}>Host</div>
                <div className={styles.profileSubTitle}>Nhà đầu tư</div>
                {/* 5 IconStarEmpty */}
              </div>
            </div>
            <div className={styles.headerContactRight}>
              <IconRibbon />
              <ButtonCall />
            </div>
          </div>
          <div className={styles.detailsContact}>
            {defaultFields.map((field, index) => (
              <div key={index} className={styles.textWithLabel}>
                <div className={styles.textLabel}>{field.label}</div>
                <div className={styles.textValue}>{field.value}</div>
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export default injectIntl(memo(ContactInfo));
