import React from "react";
import CardMultiLabels from "./CardMultiLabels";
import { useIntl } from "react-intl"

const Juridicalsinformation = (props) => {
  const { data } = props;
  const { formatMessage: f } = useIntl()

  const leftData = [
    {
      name: "gcn",
      value: null
    },
    {
      name: "mục đích",
      value: null
    },
    {
      name: "thế chấp",
      value: null
    },
    {
      name: "giải tỏa",
      value: null
    }
  ]

  const rightData = [
    {
      name: "tranh chấp",
      value: null
    },
    {
      name: "quy hoạch",
      value: data.agencyPercent
    },
    {
      name: "hiện trạng",
      value: null
    },
  ]

  return <CardMultiLabels title={f({ id: "juridical" })} leftData={leftData} rightData={rightData} />
};

export default Juridicalsinformation;