import React from "react";

import { ReactComponent as Arrow } from '@src/assets/images/icons/arrow-right.svg'

import classNames from 'classnames'

import * as styles from '../styles/Card.module.scss'

/**
 * videos: just mp4 only
 */
const CardVideos = ({ title, videos }) => {
  const [activeIndex, setActiveIndex] = React.useState(0);

  const next = () => {
    const nextIndex = activeIndex === videos.length - 1 ? activeIndex : activeIndex + 1
    setActiveIndex(nextIndex)
  }

  const previous = () => {
    const nextIndex = activeIndex === 0 ? activeIndex : activeIndex - 1
    setActiveIndex(nextIndex)
  }

  return (
    <div>
      <div className={styles.titleWrapper}>
        <h1 className={styles.title}>{title}</h1>
        <h6 className={styles.pagingText}>{activeIndex + 1}/{videos.length}</h6>
      </div>
      <div className={styles.goldContainer}>
        <div className={styles.carouselWrapper}>
          <div className={styles.carouselWrapperFlex} data-active-carousel={-activeIndex} style={{ width: `${videos.length * 100}%` }}>
            {
              videos.map((videoSrc, index) => (
                <video key={index} className={styles.img} width="320" height="240" controls>
                  <source src={videoSrc} type="video/mp4" />
                  Your browser does not support the video tag.
                </video>
              ))
            }
          </div>
        </div>
        <div className={classNames({
          [styles.btnArrowLeft]: true,
          [styles.hidden]: activeIndex === 0
        })} onClick={previous}>
          <Arrow className={styles.leftArrow} />
        </div>
        <div className={classNames({
          [styles.btnArrowRight]: true,
          [styles.hidden]: activeIndex === videos.length - 1
        })} onClick={next}>
          <Arrow className={styles.rightArrow} />
        </div>
      </div>
    </div>
  );
};
export default CardVideos;
