import React from "react";

import { Row, Col } from "reactstrap"

import * as styles from '../styles/Card.module.scss'

const CardColumn = ({ data }) => (
  <div>
    {data.map(({ text }, index) => (
      <div key={index} className={styles.row}>
        <input type="checkbox" />
        <div className={styles.text}>
          {text}
        </div>
      </div>
    ))}
  </div>
)

const CardMultiCheckboxes = ({ title, leftData, rightData }) => {
  return (
    <div>
      <h1 className={styles.title}>{title}</h1>
      <Row>
        <Col>
          <CardColumn data={leftData} />
        </Col>
        <Col>
          <CardColumn data={rightData} />
        </Col>
      </Row>
    </div>
  );
};
export default CardMultiCheckboxes;
