import React from "react";
import Card360 from "./Card360";
import { useIntl } from "react-intl"

const House360 = (props) => {
  const { data } = props;
  const { formatMessage: f } = useIntl()

  const images = [
    "https://panoraven.com/en/embed/f9cViBNf2V"
  ]

  return <Card360 title={f({ id: "image360" })} images={images} />
};

export default House360;