import React from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { useForm, Controller } from 'react-hook-form';
import ApproveRejectRes from '@services/approveRejectRes';
import classNames from 'classnames';
import { Modal, Input, notification } from 'antd';
import { ReactComponent as FlagIcon } from '@src/assets/images/icons/flag.svg';
import { ReactComponent as Approve } from '@src/assets/images/icons/approve.svg';
import { ReactComponent as Reject } from '@src/assets/images/icons/reject.svg';
import { ReactComponent as CheckCircle } from '@assets/icons/CheckCircle.svg';
import { ReactComponent as CloseIcon } from '@src/assets/images/icons/file-icons/Close.svg';
import { injectIntl, useIntl } from 'react-intl';
import { TabContent, TabPane } from 'reactstrap';
import Avatar from '@components/avatar';
import data from '../FakeData.json';

import * as styles from '../styles/ResDetailHeader.module.scss';

const { TextArea } = Input;
const ROUTE_SELECTED = '/dashboard/PagesSidebar/WaitingApproval';
const DELAY = 500;

const profileUserData = {
  id: data.profileUser.id,
  avatar: data.profileUser.avatar,
  fullName: data.profileUser.fullName,
  status: data.profileUser.status.replace,
  role: data.profileUser.role,
};

function ResDetailHeader({ dataOrigin, intl }) {
  const { id } = useParams();
  const { formatMessage: f } = useIntl();
  const history = useHistory();
  const { control, getValues } = useForm({
    defaultValues: {
      reason: '',
    },
  });

  const onReject = async () => {
    const resId = window.location.href?.split('/')?.at(-1);
    const reason = getValues('reason');
    if (!reason) {
      const errMsg = f({ id: 'pleaseEnterTheRejectReason' });
      notification.error({ message: errMsg });
      return Promise.reject(new Error(errMsg));
    }
    return ApproveRejectRes.rejectRes(resId, dataOrigin.approveStatus, reason);
  };

  const onApprove = async () => {
    const resId = window.location.href?.split('/')?.at(-1);
    return ApproveRejectRes.approveRes(resId, dataOrigin.approveStatus);
  };

  const modalControl = (approve) => {
    const msg = f({ id: 'pleaseEnterTheRejectReason' });
    const approveTitle = f({ id: 'confirmApproveRealEstate' });
    const rejectTitle = f({ id: 'confirmRejectRealEstate' });
    Modal.confirm({
      icon: approve ? <CheckCircle /> : <CloseIcon />,
      title: approve ? approveTitle : rejectTitle,
      content: !approve && (
        <>
          <p>
            <i>{msg}</i>
          </p>
          <Controller
            name="reason"
            control={control}
            render={({ field }) => (
              <TextArea {...field} allowClear showCount maxLength={255} autoSize={{ minRows: 3, maxRows: 10 }} />
            )}
          />
        </>
      ),
      onOk: async () => {
        const fn = approve ? onApprove : onReject;
        await fn();
        notification.success({
          message: approve ? f({ id: 'approveRealEstateSuccessfully' }) : f({ id: 'rejectRealEstateSuccessfully' }),
        });
        setTimeout(() => history.push(ROUTE_SELECTED), DELAY);
      },
      cancelText: f({ id: 'cancel' }),
      className: classNames(styles.modalControl, approve ? styles.approveModal : styles.rejectModal),
      okText: approve ? f({ id: 'approve' }) : f({ id: 'reject' }),
    });
  };

  return (
    <div>
      <TabContent activeTab="1" className={styles.tabContent}>
        <TabPane tabId="1">
          <div className={styles.tabPanelContent}>
            <div className={styles.headerRow}>
              <div className={styles.firstCol}>
                <span className={styles.id}>#{('000000' + id).slice(-6)}</span>
                <div className={styles.firstRow}>
                  <Avatar className={styles.avatarIcon} img={profileUserData.avatar} imgHeight="24" imgWidth="24" />
                  <FlagIcon className={styles.flagIcon} />
                  <button type="button" className={styles.btnHandle}>
                    {intl.formatMessage({ id: 'NeedToHandle' })}
                  </button>
                </div>
              </div>

              <div className={styles.line}></div>

              <div className={styles.childCol}>
                <span className={styles.cardTitle}>{intl.formatMessage({ id: 'CollectionPoint' })}</span>
                <div className={styles.headerText}>{dataOrigin.realEstateCollectionPoint || '---'}</div>
              </div>

              <div className={styles.childCol}>
                <span className={styles.cardTitle}>{intl.formatMessage({ id: 'EvaluationPoint' })}</span>
                <div className={styles.headerText}>{dataOrigin.realEstateRatePoint || '---'}</div>
              </div>

              <div className={styles.childCol}>
                <span className={styles.cardTitle}>{intl.formatMessage({ id: 'SellingPrice' })}</span>
                <div className={styles.headerText}>
                  {dataOrigin.realEstateValueSalePrice
                    ? `${(dataOrigin.realEstateValueSalePrice / 1000000000).toFixed(1)} tỷ `
                    : '---'}
                </div>
              </div>

              <div className={styles.childCol}>
                <span className={styles.cardTitle}>{intl.formatMessage({ id: 'PricePerSquare' })}</span>
                <div className={styles.headerText}>
                  {dataOrigin.realEstateUnitPrice
                    ? `${(dataOrigin.realEstateUnitPrice / 1000000).toFixed(1)} triệu`
                    : '---'}
                </div>
              </div>

              <div className={styles.childCol}>
                <span className={styles.cardTitle}>{intl.formatMessage({ id: 'NewsType' })}</span>
                <div className={styles.headerText}>{dataOrigin.realEstatePostTypeId || '---'}</div>
              </div>

              <div className={styles.line}></div>

              <div className={styles.childCol}>
                <div className={styles.btnsGroup}>
                  <Approve className={styles.btn} onClick={() => modalControl(true)} />
                  <Reject className={styles.btn} onClick={() => modalControl(false)} />
                </div>
              </div>
            </div>
          </div>
        </TabPane>
      </TabContent>
    </div>
  );
}

export default injectIntl(ResDetailHeader);
