import React, { memo, useState } from 'react';
import { Nav, NavItem, NavLink } from 'reactstrap';
import classNames from 'classnames';
import { injectIntl } from 'react-intl';
import ResInfo from './ResInfo';

import * as styles from '../styles/Tabs.module.scss';

function Tabs({ tabList, dataOrigin, setDataOrigin, intl }) {
  const [active, setActive] = useState(Object.keys(tabList)[0]);
  const tabsContent = {
    tab1: <ResInfo intl={intl} dataOrigin={dataOrigin} setDataOrigin={setDataOrigin} />,
  };

  const toggle = (tab) => {
    if (active !== tab) {
      setActive(tab);
    }
  };

  return (
    <div className={styles.tabs}>
      <Nav tabs className={classNames(styles.navTab)}>
        {Object.keys(tabList).map((key, index) => (
          <NavItem key={index}>
            <NavLink
              className={classNames(styles.navLink, { [styles.activeTab]: active === key })}
              active={active === key}
              onClick={() => {
                toggle(key);
              }}
            >
              {tabList[key]}
            </NavLink>
          </NavItem>
        ))}
      </Nav>
      {tabsContent[active] || null}
    </div>
  );
}

export default injectIntl(memo(Tabs));
