import React from "react";
import CardVideos from "./CardVideos";

import video1 from '@src/assets/videos/video1_v144P.mp4'
import video2 from '@src/assets/videos/video2_v144P.mp4'

const HouseVideos = (props) => {
  const { data } = props;

  const videos = [
    video1,
    video2
  ]

  return <CardVideos title="Video" videos={videos} />
};
export default HouseVideos;
