import React from "react";
import CardImages from "./CardImages";
import { useIntl } from "react-intl"

import sliderImage4 from '@src/assets/images/slider/04.jpg'
import sliderImage5 from '@src/assets/images/slider/05.jpg'
import sliderImage6 from '@src/assets/images/slider/06.jpg'
import sliderImage7 from '@src/assets/images/slider/06.jpg'

const LandBlueprint = (props) => {
  const { data } = props;
  const { formatMessage: f } = useIntl()

  const images = [
    sliderImage4,
    sliderImage5,
    sliderImage6,
    sliderImage7,
  ]

  return <CardImages title={f({ id: "imageBlueprint" })} images={images} />
};

export default LandBlueprint;