import { removeEmptyProperties } from '@src/helper/object';
import { ENDPOINTS } from '@src/constants/endpoints';
import { notification } from 'antd';
import { REALESTATE_STATUS } from '@constants/common';
import requestService from './request';

export default class ApproveRejectRes {
  static async approveRes(realEstateId, realStateStatus) {
    const endpoint =
      realStateStatus === REALESTATE_STATUS.WAITING_CLEANED_APPROVAL
        ? ENDPOINTS.APPROVAL_CLEAN_LIST
        : ENDPOINTS.APPROVAL_VERIFY_LIST;
    const data = {
      approvalList: [realEstateId],
    };
    const response = await requestService
      .send({
        method: 'POST',
        path: endpoint,
        data: removeEmptyProperties(data),
      })
      .then((res) => !res?.error && res?.data)
      .catch((err) => notification.error({ message: err.message }));
    return response;
  }

  static async rejectRes(realEstateId, realStateStatus, reason) {
    const endpoint =
      realStateStatus === REALESTATE_STATUS.WAITING_CLEANED_APPROVAL
        ? ENDPOINTS.REJECT_CLEAN_LIST
        : ENDPOINTS.REJECT_VERIFY_LIST;
    const data = {
      rejectList: [realEstateId],
      note: reason,
    };
    const response = await requestService
      .send({
        method: 'POST',
        path: endpoint,
        data: removeEmptyProperties(data),
      })
      .then((res) => !res?.error && res?.data)
      .catch((err) => notification.error({ message: err.message }));
    return response;
  }
}
