import React from "react";
import CardImages from "./CardImages";
import { useIntl } from "react-intl"

import sliderImage6 from '@src/assets/images/slider/06.jpg'
import sliderImage7 from '@src/assets/images/slider/06.jpg'

const HouseImages = (props) => {
  const { data } = props;
  const { formatMessage: f } = useIntl()

  const images = [
    sliderImage6,
    sliderImage7,
  ]

  return <CardImages title={f({ id: "imageHouse" })} images={images} />
};

export default HouseImages;