import React from "react";
import CardImages from "./CardImages";
import { useIntl } from "react-intl"

import sliderImage1 from '@src/assets/images/slider/01.jpg'
import sliderImage2 from '@src/assets/images/slider/02.jpg'
import sliderImage3 from '@src/assets/images/slider/03.jpg'

const CertificateOfOwnership = (props) => {
  const { data } = props;
  const { formatMessage: f } = useIntl()

  const images = [
    sliderImage1,
    sliderImage2,
    sliderImage3
  ]

  return <CardImages title={f({ id: "imageHouseOwnershipCertificateData" })} images={images} />
};

export default CertificateOfOwnership;