import React from "react";
import CardMultiLabels from "./CardMultiLabels";
import { useIntl } from "react-intl"

const RealInformation = (props) => {
  const { data } = props;
  const { formatMessage: f } = useIntl()
  const leftData = [
    {
      name: "mặt tiền",
      value: data.realEstateLandWidth,
      unit: "m",
    },
    {
      name: "chiều dài",
      value: data.realEstateLandLongs,
      unit: "m",
    },
    {
      name: "dt đất",
      value: data.realEstateLandRealitySquare,
      unit: "m²",
    },
    {
      name: "công nhận",
      value: data.realEstateLandDefaultSquare,
      unit: "m²",
    },
  ];

  const rightData = [
    {
      name: "dtnn",
      value: data.realEstateLandRealConstructionSquare,
      unit: "m²",
    },
    {
      name: "dt lộ giới",
      value: data.realEstateLandRoadSquare,
      unit: "m²",
    },
    {
      name: "hình dáng",
      value: data.shapeName
    }
  ];

  return <CardMultiLabels title={f({ id: "realEstateInfo" })} leftData={leftData} rightData={rightData} />
};

export default RealInformation;