import React from "react";

import { ReactComponent as Arrow } from '@src/assets/images/icons/arrow-right.svg'

import classNames from 'classnames'

import * as styles from '../styles/Card.module.scss'

const CardMaps = ({ title, maps }) => {
  const [activeIndex, setActiveIndex] = React.useState(0);

  const next = () => {
    const nextIndex = activeIndex === maps.length - 1 ? activeIndex : activeIndex + 1
    setActiveIndex(nextIndex)
  }

  const previous = () => {
    const nextIndex = activeIndex === 0 ? activeIndex : activeIndex - 1
    setActiveIndex(nextIndex)
  }

  return (
    <div>
      <div className={styles.titleWrapper}>
        <h1 className={styles.title}>{title}</h1>
        <h6 className={styles.pagingText}>{activeIndex + 1}/{maps.length}</h6>
      </div>
      <div className={styles.goldContainer}>
        <div className={styles.carouselWrapper}>
          <div className={styles.carouselWrapperFlex} data-active-carousel={-activeIndex} style={{ width: `${maps.length * 100}%` }}>
            {
              maps.map((mapSrc, index) => (
                <iframe
                  key={index}
                  title={index}
                  src={mapSrc}
                  width="600"
                  height="450"
                  style={{ border: 0 }}
                  allowFullScreen=""
                  loading="lazy"
                  referrerPolicy="no-referrer-when-downgrade"
                ></iframe>
              ))
            }
          </div>
        </div>
        <div className={classNames({
          [styles.btnArrowLeft]: true,
          [styles.hidden]: activeIndex === 0
        })} onClick={previous}>
          <Arrow className={styles.leftArrow} />
        </div>
        <div className={classNames({
          [styles.btnArrowRight]: true,
          [styles.hidden]: activeIndex === maps.length - 1
        })} onClick={next}>
          <Arrow className={styles.rightArrow} />
        </div>
      </div>
    </div>
  );
};
export default CardMaps;
