import React from "react";

import { Row, Col } from "reactstrap"

import * as styles from '../styles/Card.module.scss'

const CardColumn = ({ data }) => (
  <div>
    {data.map(({ name, unit, value }, index) => (
      <div key={index} className={styles.row}>
        <div className={styles.name}>{name}</div>
        <div className={styles.value}>
          <div>{value ? [value, unit].filter(x => x).join(' ') : '---'}</div>
        </div>
      </div>
    ))}
  </div>
)

const CardMultiLabels = ({ title, leftData, rightData }) => {
  return (
    <div>
      <h1 className={styles.title}>{title}</h1>
      <Row>
        <Col>
          <CardColumn data={leftData} />
        </Col>
        <Col>
          <CardColumn data={rightData} />
        </Col>
      </Row>
    </div>
  );
};
export default CardMultiLabels;
