import React, { memo, useEffect, useState } from 'react';
import { ReactComponent as FileDocumentIcon } from '@src/assets/images/icons/file-document.svg';
import { injectIntl } from 'react-intl';
import RealEstateServices from '@services/post';
import { notification } from 'antd';
import Tabs from './components/Tabs';
import ResDetailHeader from './components/ResDetailHeader';
import 'antd/dist/antd.css';

function ResDetail({ intl }) {
  const tabList = {
    tab1: (
      <div>
        <FileDocumentIcon className="file-document-icon" />
        &ensp;{intl.formatMessage({ id: 'GeneralInformation' })}
      </div>
    ),
  };
  const [dataOrigin, setDataOrigin] = useState({});
  const [activeTab, setActiveTab] = useState(Object.keys(tabList)[0]);
  const resId = window.location.href?.split('/')?.at(-1);
  useEffect(() => {
    RealEstateServices.getResDetail(resId)
      .then(({ approveStatus }) => setDataOrigin({ approveStatus }))
      .catch((err) => {
        notification.error({ message: err.message });
      });
  }, []);
  return (
    <div>
      <ResDetailHeader dataOrigin={dataOrigin} />
      <Tabs
        tabList={tabList}
        activeTab={activeTab}
        setActiveTab={setActiveTab}
        dataOrigin={dataOrigin}
        setDataOrigin={setDataOrigin}
      />
    </div>
  );
}

export default injectIntl(memo(ResDetail));
