import React, { memo } from 'react';
import { Container, Row, Col } from 'reactstrap';
import { injectIntl } from "react-intl";
import RealInformation from "./RealInformation";
import HouseInformation from "./HouseInformation";
import Juridicalsinformation from "./Juridicalsinformation";
import UtilitiesInformation from "./UtilitiesInformation";
import AdvantagesInformation from "./AdvantagesInformation";
import WeaknessInformation from "./WeaknessInformation";

import * as styles from '../styles/ResDetailHeader.module.scss'
import CertificateOfOwnership from './CertificateOfOwnership';
import LandBlueprint from './LandBlueprint';
import HouseImages from './HouseImages';
import HouseVideos from './HouseVideos';
import HouseMaps from './HouseMaps';
import House360 from './House360';
import ColContactInfo from './ContactInfo';

const ResInfo = ({ dataOrigin, setDataOrigin, intl }) => {
  return (
    <Container className='p-2'>
      <Row>
        <Col xs={8}>
          <Row style={title}>
            <Col>{intl.formatMessage({ id: 'address' })}</Col>
          </Row>
          <Row>
            <Col>
              <div className={styles.textLarge}>
                {
                  [dataOrigin.areaStreetName, dataOrigin.areaWardName, dataOrigin.areaDistrictName, dataOrigin.areaProvinceName]
                    .filter(x => x)
                    .join(', ')
                }
              </div>
            </Col>
          </Row>
          <hr className={styles.hr} />
          <Row>
            <Col>
              <RealInformation data={dataOrigin} setDataOrigin={setDataOrigin} />
            </Col>
            <Col>
              <HouseInformation data={dataOrigin} setDataOrigin={setDataOrigin} />
            </Col>
          </Row>
          <hr className={styles.hr} />
          <Row>
            <Col>
              <Juridicalsinformation data={dataOrigin} setDataOrigin={setDataOrigin} />
            </Col>
            <Col>
              <UtilitiesInformation data={dataOrigin} setDataOrigin={setDataOrigin} />
            </Col>
          </Row>
          <hr className={styles.hr} />
          <Row>
            <Col>
              <WeaknessInformation data={dataOrigin} setDataOrigin={setDataOrigin} />
            </Col>
            <Col>
              <AdvantagesInformation data={dataOrigin} setDataOrigin={setDataOrigin} />
            </Col>
          </Row>
          <hr className={styles.hr} />
          <Row>
            <Col>
              <CertificateOfOwnership />
            </Col>
            <Col>
              <LandBlueprint />
            </Col>
          </Row>
          <hr className={styles.hr} />
          <Row>
            <Col>
              <HouseImages />
            </Col>
            <Col>
              <HouseVideos />
            </Col>
          </Row>
          <hr className={styles.hr} />
          <Row>
            <Col>
              <House360 />
            </Col>
            <Col>
              <HouseMaps />
            </Col>
          </Row>
        </Col>
        <Col xs={4}>
          <ColContactInfo name={"contact-info"} data={dataOrigin}
            setDataOrigin={setDataOrigin} />
        </Col>
      </Row>
    </Container>
  );
};
const title = {
  color: "#544CA6",
  fontSize: "20px",
  fontWeight: "bold",
  paddingBottom: "6px",
};

export default injectIntl(memo(ResInfo));